
































import {Component, Vue} from 'vue-property-decorator';
import CertShow from '@/components/cert_card.vue';
import {Divider, Button} from 'vant';
import {CertOwn, CertSignStatus, UpChainStatus} from '../../model/certs_model';

import 'vant/lib/divider/style';
import 'vant/lib/button/style';

Vue.use(Divider).use(Button);

@Component({
    components: {CertShow},
})
export default class Process extends Vue {
    private certSignStateText: string = "";
    private upChainStateText: string = "";
    private businessAddress: string = "";
    private certOwn!: CertOwn;

    public created(): void {
        this.$store.state.title = "信息上链进度";
        if (!this.certOwn) {
            this.certOwn = this.$store.state.nowCertOwn;
        }
    }

    public mounted() {
        if (this.certOwn.attachInfo.certSignStatus === CertSignStatus.Success) {
            this.certSignStateText = "签名成功";
        } else {
            this.certSignStateText = "签名失败";
        }
        if (this.certOwn.attachInfo.upChainStatus === UpChainStatus.Success) {
            this.upChainStateText = "上链完成";
        } else if (this.certOwn.attachInfo.upChainStatus === UpChainStatus.Failure) {
            this.upChainStateText = "上链失败";
        } else {
            this.upChainStateText = "上链进行中";
        }
        this.businessAddress = this.certOwn.attachInfo.businessAddress;
    }

    public clickToHome(): void {
        this.$router.replace({name: 'cert_search'});
    }

    public beforeDestroy(): void {
        //todo 用vuex中的一个属性状态，来标识状态。页面MVVM就可实现监听状态变化
        /*if (this.certOwn) {
            const diamond = this.$store.state.data.diamond;
            diamond.unRegisterCert(this.certOwn);
            if (this.certOwn.hasOwn()) {
                diamond.unRegisterOwn(this.certOwn);
            }
        }*/
    }
}
